.contenedor-principal {
  background: #119e9c;
  margin: 10rem auto;
  overflow: hidden;
  padding-bottom: 5rem;
  position: relative;
  width: 90%;
}
@media (min-width: 480px) {
  .contenedor-principal {
    width: 70rem;
  }
}

.contenedor-principal::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  border-width: 0 60px 60px 0;
  border-style: solid;
  border-color: #FFFFFF #FFFFFF #074241 #074241; /* color borde y pestaña */
}

.titulo-inicio {
  font-family: "Caveat", cursive;
  color: #FFFFFF;
  font-weight: 700;
  font-size: 5rem;
}

.formulario {
  background-color: #566575;
  min-width: 100%;
  margin: 0 auto;
  padding: 3rem 2rem 3rem 2rem;
  border-radius: 1rem;
}
.formulario fieldset {
  border-color: #F5C538;
  border-radius: 1rem;
}
.formulario fieldset legend {
  font-size: 1.8rem;
  text-align: center;
  text-transform: capitalize;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .formulario fieldset legend {
    text-align: left;
  }
}
.formulario fieldset:nth-child(2), .formulario fieldset:nth-child(3) {
  margin-top: 3rem;
}
.formulario .fotografia {
  width: 30%;
  margin: 0 auto;
  border-radius: 50%;
}

.campo {
  margin-bottom: 1rem;
}
.campo label {
  color: #FFFFFF;
  font-weight: bold;
  margin-bottom: 0.5rem;
  display: block;
}
.campo textarea {
  height: 15rem;
  resize: none;
}

.file-input {
  max-width: 20rem;
  background-color: #F5C538;
  border: 1px solid #FFFFFF;
  border-radius: 0.5rem;
}

.input-text {
  width: 100%;
  min-height: 4rem;
  border-radius: 0.5rem;
}

@media (min-width: 768px) {
  .contenedor-campos {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
  .text-restrict {
    grid-row: 4/5;
  }
  .input-largo {
    grid-column: 1/3;
  }
}
.checkbox-wrapper {
  position: relative;
  display: block;
  overflow: hidden;
}
.checkbox-wrapper input[type=checkbox] {
  visibility: hidden;
  display: none;
}
.checkbox-wrapper .label {
  cursor: pointer;
}
.checkbox-wrapper .check {
  width: 50px;
  height: 50px;
  position: absolute;
  opacity: 0;
}
.checkbox-wrapper .label svg {
  vertical-align: middle;
}
.checkbox-wrapper .path1 {
  stroke-dasharray: 400;
  stroke-dashoffset: 400;
  transition: 0.5s stroke-dashoffset;
  opacity: 0;
}
.checkbox-wrapper .check:checked + label svg g path {
  stroke-dashoffset: 0;
  opacity: 1;
}

.checkbox-wrapper *,
.checkbox-wrapper ::after,
.checkbox-wrapper ::before {
  box-sizing: border-box;
  user-select: none;
}

.alerta {
  margin: 1rem;
  text-align: center;
  font-weight: bold;
  text-transform: uppercase;
}
.alerta.error {
  background-color: #9e1d0e;
  color: #FFFFFF;
}
.alerta.exito {
  background-color: #59a61e;
  color: #FFFFFF;
}

.contenedor-tabla {
