}
table.elementos thead {
  background-color: #F59138;
}
table.elementos thead th {
  color: #FFFFFF;
  padding: 1rem;
}
table.elementos tbody {
  text-align: center;
}
table.elementos tbody tr {
  background-color: whitesmoke;
}
table.elementos tbody td {
  border-bottom: 2px solid #e1e1e1;
}
table.elementos tbody td:last-of-type {
  border-bottom: none;
}
table.elementos tbody td.imagen-tabla img {
  width: 15rem;
  margin: 1rem auto;
}
table.elementos tbody td .alinear-btn {
  display: flex;
  justify-content: space-evenly;
}

#sidebar {
  position: fixed;
  width: 15rem;
  height: 100%;
  background-color: #F59138;
  top: 0;
  left: -15rem;
  transition: all 200ms linear;
}
#sidebar ul li {
  color: #FFFFFF;
  font-size: 1.5rem;
  text-align: center;
  padding: 1rem 0.5rem;
  margin: 2rem 0.5rem;
  list-style: none;
  border-radius: 1rem;
}
#sidebar ul li.img-logo {
  display: block;
  margin: 0 auto;
}
#sidebar ul li.img-logo img {
  border-radius: 50%;
}
#sidebar ul li a {
  color: #FFFFFF;
}
#sidebar ul li:hover {
  background-color: #f9b981;
  cursor: pointer;
}
#sidebar ul li.btn-active {
  background-color: #eeb50c;
}
#sidebar .toggle-btn {
  position: absolute;
  left: 16rem;
  top: 1.5rem;
  cursor: pointer;
}
#sidebar .toggle-btn i {
  display: block;
  text-align: center;
  font-size: 3rem;
  border: 2px solid #000000;
  border-radius: 0.5rem;
}
#sidebar.active {
  left: 0;
}

.cont-principal {
  background-color: #e6e6e6;
  margin: 1rem;
  border-radius: 1rem;
  padding: 1rem 5rem;
  min-height: calc(100vh - 2rem);
  transition: all 200ms linear;
  width: calc(100% - 2rem);
}